export function dateFormat(date) {
  return new Date(date).toLocaleString().slice(0, 10);
}

export function getSawPayModalCookie() {
  let par = document.cookie;
  par = par.match(new RegExp("saw_pay_modal" + "=([^&=]+)"));

  return par ? par[1].split(";")[0] : false;
}

export function getPayMethodCookie() {
  let par = document.cookie;
  par = par.match(new RegExp("pay_method" + "=([^&=]+)"));

  return par ? par[1].split(";")[0] : false;
}
